<template>
  <div class="imgIndex2">
   <i class="btn custom-icon" @click="goBack" ></i> 
    <!-- 左侧部分，包含图片 -->
    <div class="left">
      <img :src="getImageUrl(userObj.imgList[0])" width="100%" height="100%" />
    </div>

    <!-- 右侧部分，包含标题和段落 -->
    <div class="right">
      <img :src="getImageUrl(userObj.imgList[1])" width="100%" height="100%" />
    </div>
  </div>
</template>
<script>
export default {
  name: "imgIndex2",
  data() {
    return {
      userObj: {},
    };
  },
  beforeCreate() {},
  created() {},
  beforeMount() {},
  mounted() {
    // 获取存储的字符串
    const storedUser = sessionStorage.getItem("data");

    if (storedUser) {
      // 将字符串解析为对象
      this.userObj = JSON.parse(storedUser);
      console.log(this.userObj); // { name: "John", age: 30 }
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1); // 返回上一个页面
    },
    getImageUrl(imgPath) {
      // 在 Vue 项目中，通过 require() 动态加载图片log
      return require(`@/assets/${imgPath}`);
    },
  },
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  computed: {}, // 计算属性
};
</script>
 
<style scoped >
.imgIndex2 {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between; /* 左右分布 */
  align-items: center; /* 垂直居中 */
  padding: 20px;
  background: #f2f2f2; /* 容器的背景色 */
  color: white;
}
.left {
  /* border: 1px solid #000; */
  flex: 1; /* 使左侧部分占用剩余空间 */
  width: 100%;
  height: 100%;
  padding: 15px 10px 15px 30px;
}

.left img {
  max-width: 100% !important;  /* 限制最大宽度为父元素宽度 */
  height: auto !important;     /* 自动调整高度以保持宽高比 */

  height: auto; /* 保持图片比例 */
  /* border: 1px solid #000; */

}

.right {
  flex: 1; /* 使右侧部分占用更多空间 */
  padding: 15px 10px 15px 30px;
  height: 100%;
  width: 100%;
  /* border: 1px solid #000; */
}
.right img {
  max-width: 100% !important;  /* 限制最大宽度为父元素宽度 */
  /* height: auto !important;     自动调整高度以保持宽高比 */

  max-height: 100% !important; /* 保持图片比例 */
  /* border: 1px solid #000; */

}

.btn{
  position: absolute;
  top: 48%;
  left: 40px;
}
.custom-icon {
  background: url('../assets/back/go4.png') 
  no-repeat center center;
  width: 80px; /* 设置按钮图标大小 */
  height: 50px;
  background-size: 200% 200%; 
  object-fit: contain; /* 确保图片按比例自适应 */
  z-index: 99999;
  margin-left: -15px;
}
</style>


